import React from 'react';
import Button from './Button';

const ButtonList = ({ choices }) => {
    return (
        <div className="tc">
            {
                choices.map((a, i) => {
                    return (
                        <Button key={i} 
                        choice={choices[i].name} 
                        effect={choices[i].effect}
                        />
                    )
                })
            }
        </div>
    );
}

export default ButtonList;


