import React from 'react'

const ScoreCard = ({ scores }) => {
    let { humanScore, cpuScore, tieScore, round } = scores;
  return (
    <div className="tc">
        <div>Human: {humanScore} / CPU: {cpuScore} / Tie: {tieScore}</div>
        <div>Round: {round}</div>
    </div>
  )
}


export default ScoreCard;



