import React from 'react';
import './Button.css';

const Button = ({ choice, effect }) => {
        return (
            <button className="throw-buttons f6 f5-ns fw6 dib ba
            b--black-20 bg-light-blue
            ph3 ph4-ns pv2 pv3-ns br2
            grow no-underline" onClick={effect}>{choice}</button>
        )
}

export default Button;


