import React, { Component } from 'react';
import ButtonList from '../components/ButtonList';
import ScoreCard from '../components/ScoreCard';
import Title from '../components/Title';
import Results from '../components/Results';
import Announce from '../components/Announce';


class App extends Component {
  constructor() {
    super();
    this.state = {
        humanScore: 0,
        cpuScore: 0,
        tieScore: 0,
        round: 0,
        human: '',
        cpu: '',
    }
  }

  render() {
    let winner = '';

    const throws = [
      {number: 1, name: 'rock', effect: () => game(1)},
      {number: 2, name: 'paper', effect: () => game(2)},
      {number: 3, name: 'scissor', effect: () => game(3)},
    ];
    //game code

    // updates state
    let updateScore = (winner) => {
      if (winner === 1) {
        this.setState({humanScore: this.state.humanScore + 1});
      } else if (winner === 2) {
        this.setState({cpuScore: this.state.cpuScore + 1})
      } else if (winner === 3) {
        this.setState({tieScore: this.state.tieScore + 1})
      }
      this.setState({round: this.state.round + 1});
    }

    // round winner text
    let roundWinner = () => {
      let text = '';
      if (this.state.human === 1) {
        text += 'You threw rock, CPU threw ';
        if(this.state.cpu === 1) {
          text += 'rock, we have a tie!';
        } else if (this.state.cpu === 2) {
          text += 'paper, you lose this round.';
        } else {
          text += 'scissor, you win this round!';
        }
      } else if (this.state.human === 2) {
        text += 'You threw paper, CPU threw ';
        if(this.state.cpu === 1) {
          text += 'rock, you win this round!';
        } else if (this.state.cpu === 1) {
          text += 'paper, we have a tie!';
        } else {
          text += 'scissor, you lose this round.';
        }
      } else if (this.state.human === 3) {
        text += 'You threw scissor, CPU threw ';
        if (this.state.cpu === 1) {
          text += 'rock, you lose this round.';
        } else if (this.state.cpu === 2) {
          text += 'paper, you win this round!';
        } else {
          text += 'scissor, we have a tie!';
        }
      }
      return text;
    }

    // final screen text
    let winLossText = () => {
      if (this.state.humanScore === this.state.cpuScore) {
        winner = 'Tied! Nice try!';
      } else if (this.state.humanScore > this.state.cpuScore) {
        winner = 'Congratulations! You win!';
      } else {
        winner = 'Unfortunately you lost';
      }
    }

    // main game function operated by buttons
    const game = (humanChoice) => {
      let cpuThrow = 0;
      const roll = Math.floor(Math.random()*100);
      roll >= 66 ? cpuThrow=1 : 
      roll >= 33 ? cpuThrow=2 : 
      cpuThrow=3;
      this.setState({cpu: this.state.cpu = cpuThrow});
      this.setState({human: this.state.human = humanChoice});

    // win/loss logic
    if (humanChoice === cpuThrow) {
      updateScore(3);
    } else if ((humanChoice === 1 && cpuThrow === 3) || (humanChoice === 2 && cpuThrow === 1) || (humanChoice === 3 && cpuThrow === 2)) {
      updateScore(1);
    } else {
        updateScore(2);
    }
  }

  // app
  if (this.state.round < 5) {

    let roundAnnounce = roundWinner();

    return (
      <div className="container">
        <Title greeting='React RPS' />
        <ButtonList choices={throws}/>
        <ScoreCard scores={this.state}/>
        <Announce result={roundAnnounce} />
      </div>
    )
  } else {
    
    winLossText();
  
    return (
      <div className="container">
        <Title greeting='Game Over' />
        <ScoreCard scores={this.state}/>
        <Results winner={winner} />
      </div>
    )
  }
  }
}

export default App;



